@font-face {
    font-family: Without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}

@font-face {
    font-family: Droid;
    src: url(/fonts/DroidSerif.ttf);
}

@font-face {
    font-family:Monserrat ;
    src: url(/fonts/Montserrat-SemiBold.otf);
}

body{
    margin: 0;
    padding: 0;
}

//css for deel
.deel1{
    display: flex;
    flex-direction: row;
    width: 100vw;
    align-items: center;
    justify-content: space-around;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-top: 2vh;
}

.deel1 a{
    text-decoration: none;
    color:#013335 ;
}

.logo{
    width: 100vw;
    display: flex;
    justify-content: center;
}

.logo img{
    width: 3vw;
}


.nav{
    display: flex;
    flex-direction: row;
    width: 100vw;
    justify-content: center;
}

.nav .main{
    font-weight: bolder;
    font-family: Monserrat;
}



.dropdown {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 20vw;
    position: relative; /* Important for positioning dropdown-content */
    transition: all 1s ease-in-out;
  }

  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
    visibility: hidden;
    opacity: 0; /* Ensure initial opacity is 0 */
    position: absolute; /* Position relative to dropdown */
    top: 100%; /* Position below dropdown */
    left: 5vw;
    width: 10vw;
    background-color: whitesmoke; /* Optional: Set background color */
    transition: all 1s ease-in-out;
  }

  .dropdown-content a{
    padding: 5px;
    transition: all 1s ease-in-out;
  }

  .dropdown-content a:hover{
    background-color: grey;
  }
  
  .dropdown:hover .dropdown-content {
    visibility: visible;
    opacity: 1; /* Show dropdown content on hover */
  }

.phone-links{
   
    display: none;
}

.phone-links .main{
    font-family: Monserrat;
    transition: all 1s ease-in;
}

.phone-links .main:hover{
    background-color: goldenrod;
}

.hamburger-class{
    width: 100vw;
    display: none;
    font-size:2em ;
    transition: all 1s ease-in;
}

.hamburger-class:hover .phone-links{
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    width: 35vw;
    text-align: left;
    padding-bottom: 2vw;
}

.phone-links a{
    margin-top: 2vh;
    transition: background-color 2s ease-in;
    padding-left: 2vw;
    padding-top: 0.5vh;
    padding-bottom: 0.5vh;
    padding-right: 2vw;
    font-size: medium;
}

.phone-links a:hover{
    background-color: grey;
}


@media (max-width : 1000px) {
    .deel1{
        display: flex;
        justify-content: space-around;
        align-items: flex-start;
    }
    .logo img{
        width: 10vw;
    }
    .nav{
        display: none;
    }
    .hamburger-class{
        display: block;
        margin-left: 20%;
    }
}



.swipe-in{
    transform: translateX(100%);
    animation: swipe-in 1s ease-in-out forwards;
}

.swipe-out{
    transform: translateX(0%);
    animation: swipe-out 1s ease-out forwards;
}

@keyframes swipe-in {
    from{
        opacity: 0;
        transform: translateX(100%);
    }

    to{
        opacity: 1;
        transform: translateX(0%);
    }
}

@keyframes swipe-out {
    from{
        opacity: 1;
        transform: translateX(0%);
    }

    to{
        opacity: 0;
        transform: translateX(100%);
    }
}
